.customize-container {
  padding: 24px 0 !important;
  background: linear-gradient(135deg, var(--light-bg) 0%, #f0f3f7 100%);
}

.customize-box {
  background: var(--card-bg);
  border-radius: 20px;
  padding: 32px;
  box-shadow: var(--shadow-medium);
  transition: all 0.3s ease;
}

.customize-title {
  font-size: 32px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 32px;
  text-align: center;
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--accent-color) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.tabs-wrapper {
  border-bottom: 2px solid var(--border-color);
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 24px;
  background: var(--light-bg);
}

.custom-tabs {
  min-height: 56px !important;
}

.tab-panel {
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.tab-panel .MuiTypography-root {
  display: block;
}

@media (max-width: 768px) {
  .customize-box {
    padding: 20px;
    border-radius: 16px;
  }

  .customize-title {
    font-size: 24px;
    margin-bottom: 24px;
  }

  .tabs-wrapper {
    margin-bottom: 16px;
  }
}

@media (max-width: 480px) {
  .customize-box {
    padding: 16px;
    border-radius: 12px;
  }

  .customize-title {
    font-size: 20px;
    margin-bottom: 16px;
  }

  .custom-tabs {
    min-height: 48px !important;
  }

  .custom-tabs .MuiTab-root {
    font-size: 12px !important;
    padding: 8px 12px !important;
  }
}
